export interface GameData {
  pokemon: PokemonInfo[];
  moves: Move[];
}

export interface PokemonInfo {
  dex: number;
  speciesName: string;
  speciesId: string;
  tags?: string[];
  baseStats: {
    atk: number;
    def: number;
    hp: number;
  };
  types: string[];
  fastMoves: string[];
  chargedMoves: string[];
  legacyMoves?: string[];
  eliteMoves?: string[];
  defaultIVs?: {
    cp500?: number[];
    cp1500?: number[];
    cp2500?: number[];
  };
  level25CP?: number;
  levelCap?: number;
  searchPriority?: number;
  buddyDistance?: number;
  thirdMoveCost?: number | boolean;
  released?: boolean;
  levelFloor?: number;
  level?: number;
  atkIV?: number;
  defIV?: number;
  hpIV?: number;
  currentFastMove?: Move,
  currentChargedMoves?: Move[],
  shields?: number;
  currentMove?: Move;
  energy?: number;
  buffs?: [number, number];
  atk?: number;
  def?: number;
  hp?: number;
  cp?: number;
  currentHp?: number;
}

export enum TypeOfMove {
  fast,
  charged
}

export interface Move {
  moveId: string;
  name: string;
  type: string;
  power: number;
  energy: number;
  energyGain: number;
  cooldown: number;
  archetype?: string;
  buffs?: [number, number];
  buffTarget?: string;
  buffApplyChance?: string;
  abbreviation?: string;
  elapsed?: number;
  typeOfMove?: TypeOfMove;
}

